@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

.navNotActive {
  @apply font-primary bg-white flex justify-center h-40 w-full mt-24 fixed z-40 transition-all duration-300 ease-in-out -top-full;
}

.navActive {
  @apply font-primary bg-white flex justify-center h-40 w-full mt-24 fixed z-40 transition-all duration-300 ease-in-out top-0;
}
